
.App {
  background-color: #fefefe;
  height: 100%;
  width: 100%;
}

html, body {
  height: 100%;
  margin: 0;
}
/* Flex container for page layout */
.page-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

/* Main content should grow to fill space */
.content-wrap {
  flex: 1;
}
.crimson-text-regular {
  font-family: "Crimson Text", serif;
  font-weight: 400;
  font-style: normal;
  color: #f1ebda;
}
